@import "../../Variables/Variables.scss";

.user-detail {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  margin-left: 283px;
  width: 100%;
  @media (max-width: 800px) {
    margin-left: 80px;
  }
   .go-back {
    display: flex;
    gap: 0.8125rem;
    align-items: center;
    margin-bottom: 1rem;
    .back-icon {
      cursor: pointer;
    }
    .back-text {
      padding: 0 0 0.2rem 0;
      margin: 0;
      font-size: 1rem;
      color: $secondary-text-color;
    }
  }

  .user-action {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2.5rem;
    // border: 1px solid red;

    .text {
      font-weight: 500;
      font-size: 1.5rem;
      color: $primary-text-color;
      padding: 0;
      margin: 0;
    }
    .blacklist-and-activate {
      display: flex;
      gap: 1.25rem;
    }
  }

  .primary-information {
    padding: 1.875rem 0 0 1.875rem;
    margin-bottom: 1.875rem;
    box-shadow: 3px 5px 20px rgba(0, 0, 0, 0.04);
    .user-info-display {
      display: flex;
      align-items: center;
      margin-bottom: 3.1875rem;
      flex-wrap: wrap;
      row-gap: 2rem;

      .user-image {
        width: 6.25rem;
        height: 6.25rem;
        border-radius: 50%;
        object-fit: contain;
        cursor: pointer;
        margin-right: 1.25rem;
      }
      .name-and-bvn {
        margin-right: 1.875rem;
        .name {
          padding: 0;
          margin: 0 0 0.5rem 0;
          font-size: 1.375rem;
          color: $primary-text-color;
        }
        .bvn {
          padding: 0;
          margin: 0;
          color: $secondary-text-color;
        }
      }

      .bar {
        border: 0.5px solid $secondary-text-color;
        height: 5rem;
        opacity: 0.2;
        margin-right: 1.875rem;
      }

      .users-tier {
        margin-right: 1.875rem;

        .name {
          padding: 0;
          margin: 0 0 0.625rem 0;
          color: $secondary-text-color;
        }
        .stars {
          cursor: pointer;
        }
      }

      .bank-and-amount {
        .amount {
          padding: 0;
          margin: 0 0 0.75rem 0;
          color: $primary-text-color;
          font-weight: 500;
          font-size: 1.375rem;
        }
        .bank {
          font-size: 0.75rem;
          color: $primary-text-color;
          padding: 0;
          margin: 0;
        }
      }
    }

    .user-info-nav {
      display: flex;
      overflow-x: scroll;
      gap: 0.5rem;
      justify-content: space-between;
      row-gap: 2rem;
    }
  }

  .secondary-information {
    box-shadow: 3px 5px 20px rgba(0, 0, 0, 0.04);
    padding: 0 1.875rem 1rem;
    height: fit-content;
  }

  .no-content {
    text-align: center;
    font-size: 1.2rem;
    color: $primary-text-color;
    font-weight: 500;
    margin: 5rem 0;
  }

  @media (max-width: 1030px) {
    .primary-information {
      .user-info-nav {
        justify-content: flex-start;
        row-gap: 1.5rem;
      }
    }
  }

  @media (max-width: 1006px) {
    .primary-information {
      .user-info-display {
        justify-content: center;
        .users-tier {
          padding-right: 2rem;
        }
        .user-tier-bar-hidden {
          display: none;
        }
      }
    }
  }

  @media (max-width: 970px) {
    .user-action {
      flex-direction: column;
      align-items: center;
      gap: 1.5rem;
    }
    .primary-information {
      .user-info-display {
        padding-right: 0.5rem;
      }
    }
  }

  @media (max-width: 843px) {
    .primary-information {
      .user-info-display {
        .name-bar-hidden {
          display: none;
        }
        .name-and-bvn {
          padding-right: 0.3rem;
        }
      }
    }
  }

  @media (max-width: 600px) {
    .user-action {
      .text {
        font-size: 1rem;
      }
    }
  }

  @media (max-width: 480px) {
    .primary-information {
      .user-info-nav {
        row-gap: 1rem;
      }
    }
    .user-action {
      .blacklist-and-activate {
        flex-direction: column;
        align-items: center;
      }
    }
  }

  @media (max-width: 400px) {
    .primary-information {
      padding: 1.875rem 0 0 0.75rem;
    }
  }

  @media (max-width: 350px) {
    .secondary-information {
      // padding: 0;
      padding: 0 0.625rem 0rem;
    }
  }
}
