@import "../../Variables/Variables.scss";

.container {
  width: auto;
  height: auto;
  display: flex;
  gap: 0.625rem;
  align-items: center;
  padding-left: 1.875rem;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  user-select: none;
  opacity: 0.6;

  &:hover {
    background-color: #f3fcfc;
    padding-left: 2.3rem;
  }

  .image {
    width: 1rem;
    height: 0.8825rem;
    
    @media (max-width: 800px) {
      padding: 0.625rem 0;
  }
  }

  .title {
    padding: 0.625rem 0;
    margin: 0;
    font-size: 1rem;
    color: $primary-text-color;  
     @media (max-width: 800px) {
        font-size: 0.625rem; 
         display: none;
    }
  }

 
  @media (max-width: 650px) {
    padding-left: 0;

    &:hover {
      padding-left: 0.75rem;
    }
    .title {
      display: none;
    }
    .image {
      display: block;
      padding: 0.625rem 0;
      margin: auto;
    }
  }
}

.active {
  background-color: #f3fcfc;
  border-left: 3px solid $tertiary-text-color;
  opacity: 1;
  &:hover {
    background-color: #f3fcfc;
  }
}
